@font-face {
  font-family: Cyber;
  src: url("https://assets.codepen.io/605876/Blender-Pro-Bold.otf");
  font-display: swap;
}

body {
  background: url("https://cdn.discordapp.com/attachments/468501401350176798/873565567934660648/bg.jpg");
  background-size: auto;
  overflow: hidden;
  height: 100vh;
}

.homePage {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.character {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.character_head {
  height: 25vh;
  z-index: 1;
}
.character_body {
  margin-top: -22vh;
  height: 75vh;
}

.character_fists {
  position: relative;
  bottom: 60%;
  width: 200%;
  display: flex;
  justify-content: space-around;
}

.character_fists img:nth-child(1) {
  position: absolute;
  transition: 0.5s;
  left: 0%;
}

.left_fist_active {
  transition: 0.1s !important;
  left: 35% !important;
}
.right_fist_active {
  transition: 0.1s !important;
  right: 35% !important;
}

.character_fists img:nth-child(2) {
  position: absolute;
  transition: 0.5s;
  right: 0;
}

.display {
  position: relative;
}

.display .hp {
  font-weight: 900;
  position: absolute;
  top: 40px;
  right: 15%;
  font-size: 1.2rem;
  color: rgb(218, 218, 218);
}
.display .lvl {
  font-weight: 900;
  position: absolute;
  top: 40px;
  right: 23%;
  font-size: 1.2rem;
  color: black;
}

.display .armor {
  position: absolute;
  top: 40%;
  right: 30%;
}
.display .helmet {
  position: absolute;
  top: 40%;
  right: 40%;
}
