.links {
    position: fixed;
    height: 100vh;
    width: 150px;
    background-color: rgba(0, 0, 0, 0.602);
    transition: .3s;
    opacity: 0.1;
    font-size: 1.2rem;
    font-weight: bold;
    text-align: center;
}

.links a {
    color: white;
}

.links:hover {
    transition: .3s;
    opacity: 1;
}